import { Component } from '@angular/core';
import { NavController, ModalController, LoadingController, AlertController, MenuController } from 'ionic-angular';
import * as firebase from 'firebase';
import { AuthProvider } from '../../providers/auth/auth';
import { AngularFireAuth } from 'angularfire2/auth';

@Component({
  selector: 'page-admin',
  templateUrl: 'admin.html',
})
export class AdminPage {

  users;
  
  constructor(public navCtrl: NavController, public menuCtrl: MenuController, public alertCtrl: AlertController, public modalCtrl: ModalController, private loadingCtrl: LoadingController, private authData: AuthProvider, private afAuth: AngularFireAuth) {
    let loader = this.loadingCtrl.create({
      content: 'Please Wait, Initializing Data...',
      spinner: 'crescent',
      duration: 2000
    });
    loader.present();

    setTimeout(() => {
      this.initData();
    }, 2000);
  }

  toggleMenu() {
    this.menuCtrl.toggle();
  }

  initData() {
    let ref = firebase.database().ref('user/');
    ref.on('value', data => {
      let arr = [];   
      data.forEach(data => {
       let reference = data.val().status;
       let status: boolean;
       if(reference == 1){status = true;}
       else if(reference == 2){status = false;}
 
        if(data.val().usertypeid != 3){
          arr.push({
            key: data.key,
            email: data.val().email,
            name: data.val().name,
            address: data.val().address,
            status: status,
            profilePicUrl: data.val().profilePicUrl,
            usertypeid: data.val().usertypeid
          });
        }
       
      });
      this.users = arr;
    });
   }
 
   updateStatus(user){
     let loader = this.loadingCtrl.create();
     loader.present();

     let status;
     let ref = firebase.database().ref('user/' + user.key);

     if(user.status){status = "1";}
     else if(!user.status){status = "2";}
 
     setTimeout(() => {
        ref.set({
          email: user.email,
          address: user.address,
          name: user.name,
          status: status,
          profilePicUrl: user.profilePicUrl,
          usertypeid: user.usertypeid
        }).then(() => {
          loader.dismiss();
        });
     }, 2000);
   }
}
 


